* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #af8dee;
  color: #212121;
  line-height: 1.5;
  margin: 0;
  padding: 30px;
}

#search-box {
  font: inherit;
  padding: 8px;
  min-width: 200px;
  max-width: 320px;
  margin-bottom: 10px;
  border: none;
  border-radius: 10px;
  box-shadow: 0 5px 10px rgba(154, 160, 185, 0.05),
    0 15px 40px rgba(0, 0, 0, 0.1);
}


.country-input {
  width: 300px;
  margin-bottom: 10px;
}

.country-list {
  list-style: none;
  padding: 0;
}

.country-list-item {
  display: flex;
  justify-content: start;
  align-items: center;
}

.country-list-item:not(:last-child) {
  margin-bottom: 10px;
}

.country-info__flag {
  width: 30px;
  margin-right: 10px;
}

.country-info__list {
  list-style: none;
  padding: 0;
}

.country-info__list-item:not(:last-child) {
  margin-bottom: 20px;
}

.country-info__name {
  font-weight: 700;
}
